canvas {
  font-family: "Segoe UI Historic", "Noto Sans Egyptian Hieroglyphs", "Arial Unicode MS", monospace;
}

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;   /* Empêche les barres de défilement */
}

#root {
  height: 100%;
}

canvas {
  display: block;
  width: 100vw !important;
  height: 100vh !important;
  position: fixed;
  top: 0;
  left: 0;
}

